import React from 'react';
import S from "./Shop.module.css";
import X from "../img/X.svg";
import {DisplayItem} from "../../../Body/Menu/MainDIsplay/MainDisplay";


export type ShopType = {
  state: boolean
  onClick: () => void
  addedCart: DisplayItem[]
  setAddedCart: (addedCart: DisplayItem[]) => void;
}

export const Shop = (props: ShopType) => {

  const {state, onClick, addedCart, setAddedCart} = props

  const onClickPlus = (id: string) => {
    setAddedCart(addedCart.map(el => el.id === id ? {...el, quantity: el.quantity + 1} : el))
  }

  const onClickMinus = (id: string) => {
    const updatedCart = addedCart.map(el => el.id === id ? {...el, quantity: el.quantity - 1} : el);
    const updatedCartFiltered = updatedCart.filter(el => el.quantity > 0);
    setAddedCart(updatedCartFiltered);
  }

  const deleteItem = (id: string) => {
    setAddedCart(addedCart.filter(el => el.id !== id))
  }

  const totalPrice = props.addedCart.reduce((total, el) => total + el.price * el.quantity, 0);

  return (
    <div>
      <div className={`${S.shoppingCartContainer} ${!state ? S.active : ''}`} onClick={onClick}></div>
      <div className={`${S.shoppingCart} ${!state ? S.active : ''}`}>
        <h2>Chopping Cart</h2>
        <div className={S.listCart}>
          {addedCart.map((el) => (<div key={el.id}>
            <div className={S.shoppingContainer}>
              <img className={S.img} src={el.src} alt={el.src}/>
              <div className={S.cartContainer}>
                <div className={S.textContainer}>
                  <div className={S.title}>{el.title}</div>
                  <div className={S.svgX} onClick={() => deleteItem(el.id)}>
                    <img src={X} alt={X}/>
                  </div>
                </div>
                <div className={S.size}>Size: {el.size}</div>
                <div className={S.priceContainer}>
                  <div className={S.price}>${(el.price * el.quantity).toFixed(2)}</div>
                  <div className={S.quantityCart}>
                    <span className={S.button} onClick={() => onClickMinus(el.id)}>-</span>
                    <span>{el.quantity}</span>
                    <span className={S.button} onClick={() => onClickPlus(el.id)}>+</span>
                  </div>
                </div>
              </div>
            </div>
          </div>))}
        </div>
        <div className={S.orderPrice}>
          <div className={S.price}>Total: ${totalPrice}</div>
          <div className={S.btn}>Order</div>
        </div>
      </div>
    </div>
  );
};

