import React from 'react';
import S from './SvgArrow.module.css'

type SvgType = {
  setIndex: () => void

}

export const Prev = (props:SvgType) => {
  return (
    <div className={S.container} onClick={props.setIndex}>
      <svg className={S.svgArrow} width="60" height="60" viewBox="0 0 60 60" fill="none" xmlns="http://www.w3.org/2000/svg">
        <rect x="0.5" y="0.5" width="59" height="59" rx="29.5" stroke="#665F55"/>
        <path d="M36.5 30H24M24 30L30 24M24 30L30 36" stroke="#403F3D" strokeLinecap="round"
              strokeLinejoin="round"/>
      </svg>
    </div>
  );
};

export default Prev;