import React from 'react';
import S from './Footer.module.css'
import {ReactComponent as Geo} from './src/geo.svg'
import {ReactComponent as Phone} from './src/phone.svg'
import {ReactComponent as Clock} from './src/clock.svg'
import {Face} from "./src/Face";
import {Tg} from "./src/Tg";
import {Instagram} from "./src/Instagram";
import {NavLink} from "react-router-dom";


export const Footer = () => {
  return (
    <div id='contactUs' className={S.container}>
      <div className={S.containerAll}>
        <div className={S.text}>
          <div>Sip, Savor, Smile. <i>Its coffee time!</i></div>
          <div className={S.iconContainer}>
            <NavLink to={'https://t.me/Teiregar'} target="_blank">
              <Tg />
            </NavLink>
            <NavLink to={'https://www.instagram.com/evgkorolenia/'} target="_blank">
              <Instagram/>
            </NavLink>
            <NavLink to={'https://www.facebook.com/'} target="_blank">
              <Face/>
            </NavLink>

          </div>
        </div>
        <div className={S.contactUs}>
          <div> Contact us</div>
          <div><Geo className={S.svg}/> 108 Pobediteley Avenue, Minsk</div>
          <div><Phone className={S.svg}/> +375 (33) 328-93-99</div>
          <div><Clock className={S.svg}/> Mon-Sat: 10:00 AM  7:00 PM</div>
        </div>
      </div>
    </div>
  );
};
