import {ItemType} from "../data";
import React, {useState} from "react";
import S from './MainDisplay.module.css'
import {Svg} from "./svg/Svg";
import {v1} from "uuid";

export type DisplayItem = {
  id: string,
  title: string,
  price: number;
  size: string;
  src: string;
  quantity: number;
}

type DataDisplayProps = {
  data: ItemType[];
  addedCart: DisplayItem[];
  setAddedCart: (addedCart: (prevCart: DisplayItem[]) => ({
    id: string;
    title: string;
    price: number;
    size: string;
    src: string;
    quantity: number
  }[])) => void;
}

export const DataDisplay = ({data, addedCart, setAddedCart}: DataDisplayProps) => {

  const [activeItemId, setActiveItemId] = useState<string | null>(null);
  const [currentPrice, setCurrentPrice] = useState<number | undefined>();
  const [currentSize, setCurrentSize] = useState<string | undefined>('');
  const [currentIdProduct, setCurrentIdProduct] = useState<string | undefined>('');

  const handleSizeChange = (price: number | undefined, size: string | undefined) => {
    setCurrentPrice(price);
    setCurrentSize(size);
  }

  const handlePriceChange = (id: string | undefined) => {
    setCurrentIdProduct(id);
  }

  const handleItemClick = (coffee: ItemType) => {
    setActiveItemId(coffee.id);
    setCurrentPrice(coffee.dop.size.s.priceSize);
    setCurrentSize(coffee.dop.size.s.ml);
    setCurrentIdProduct(coffee.dop.size.s.idProd);
  }


    const AddedItems = (id: string, title: string, price: number, size: string, src: string) => {
      setAddedCart(() => {
        const itemExists = addedCart.find(item =>  item.title === title && item.price === price);
        if (itemExists) {
          return addedCart.map(item => item.title === title && item.price === price ? {
            ...item,
            quantity: item.quantity + 1
          } : item);
        } else {
          const newItem: DisplayItem = { id: v1(), title, price, size, src, quantity: 1 };
          return [...addedCart, newItem];
        }
      });
      setActiveItemId(null);
    }

      return (
        <div>
          <div className={S.container}>
            {data.map((coffee: ItemType) => (
              <div className={S.infBlock} key={coffee.id} onClick={() => {
                handleItemClick(coffee)
              }}>
                <div>
                  <div className={S.imgDiv}>
                    <img className={S.img} src={coffee.src} alt={coffee.title}/>
                  </div>
                  <div className={S.textContainer}>
                    <div className={S.titleDiv}>{coffee.title}</div>
                    <div className={S.descriptionDiv}>{coffee.description}</div>
                    <div className={S.priceDiv}>${coffee.price.toFixed(2)}</div>
                  </div>
                </div>
                <div className={`${S.modal} ${activeItemId === coffee.id ? S.active : ''}`}
                     onClick={(e) => {
                       e.stopPropagation()
                       setActiveItemId(null)
                     }}>
                  <div className={`${S.modalContent} ${activeItemId === coffee.id ? S.active : ''}`}
                       onClick={(e) => e.stopPropagation()}>
                    <div className={S.popupImg}>
                      <img src={coffee.src} alt={coffee.title}/>
                    </div>
                    <div className={S.popupTextCont}>
                      <div className={S.popupTitle}>{coffee.title}</div>
                      <div className={S.popupDescription}>{coffee.description}</div>
                      <div className={S.popupSizeContainer}>
                        <div>
                          Size
                        </div>
                        <div className={S.popupSize}>
                          <div
                            className={`${S.popupSizeS} ${(coffee.dop.size.s.priceSize) === currentPrice ? S.active : ''}`}
                            onClick={() => {
                              handleSizeChange(coffee.dop.size.s.priceSize, coffee.dop.size.s.ml);
                              handlePriceChange(coffee.dop.size.s.idProd);
                            }}>
                            <div className={S.worS}>S</div>
                            <div className={S.mlS}>{coffee.dop.size.s.ml}</div>
                          </div>
                          <div
                            className={`${S.popupSizeM} ${(coffee.dop.size.m.priceSize) === currentPrice ? S.active : ''}`}
                            onClick={() => {
                              handleSizeChange(coffee.dop.size.m.priceSize, coffee.dop.size.m.ml);
                              handlePriceChange(coffee.dop.size.m.idProd);
                            }}>
                            <div className={S.worM}>M</div>
                            <div className={S.mlM}>{coffee.dop.size.m.ml}</div>
                          </div>
                          <div
                            className={`${S.popupSizeL} ${(coffee.dop.size.l.priceSize) === currentPrice ? S.active : ''}`}
                            onClick={() => {
                              handleSizeChange(coffee.dop.size.l.priceSize, coffee.dop.size.l.ml);
                              handlePriceChange(coffee.dop.size.l.idProd);
                            }}>
                            <div className={S.worL}>L</div>
                            <div className={S.mlL}>{coffee.dop.size.l.ml}</div>
                          </div>
                        </div>
                      </div>
                      <div className={S.additivesContainer}>
                        <div>
                          Additives
                        </div>
                        <div className={S.popupAdditives}>
                          <div className={S.additivesOne}>
                            <div className={S.numOne}>1</div>
                            <div className={S.addOne}>{coffee.additives.one}</div>
                          </div>
                          <div className={S.additivesTwo}>
                            <div className={S.numTwo}>2</div>
                            <div className={S.addTwo}>{coffee.additives.two}</div>
                          </div>
                          <div className={S.additivesThree}>
                            <div className={S.numThree}>3</div>
                            <div className={S.addThree}>{coffee.additives.three}</div>
                          </div>
                        </div>
                      </div>
                      <div className={S.priceContainer}>
                        <div>Total:</div>
                        <div>${currentPrice?.toFixed(2)}</div>
                      </div>
                      <hr/>
                      <div className={S.popupInf}>
                        <Svg/>
                        <div>The cost is not final. Download our mobile app to see the final price and place your
                          order.
                          Earn loyalty points and enjoy your favorite coffee with up to 20% discount.
                        </div>
                      </div>
                      <div className={S.popupButton}>
                        <div
                          onClick={() => AddedItems(coffee.id, coffee.title, currentPrice!, currentSize!, coffee.src)}>Add
                        </div>
                        <div onClick={() => setActiveItemId(null)}>Close</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      );
    }
  ;