import React from 'react';
import S from './Burger.module.css';
import {SvgCup} from '../img/svgCup';
import {NavLink} from "react-router-dom";
import {PATH} from "../../../../App";
import {N} from "../../_stules";

type BurgerType = {
  state: boolean
  onClickRetract: () => void
  scrollToSelection: (selectionId: string) => void
}

export const Burger = (props:BurgerType) => {

  const {state, onClickRetract, scrollToSelection} = props;

  const onClickHandler = (selectionId: string) => {
    scrollToSelection(selectionId);
    onClickRetract()
  }



  return (
    <div>
      <div className={S.headerTabletContainer}>
        <div className={`${S.retractableHeader} ${!state ? S.active : ''}`}>
          <div className={S.retractableText}>
            <div onClick={() => onClickHandler('favoriteCoffee')}>Favorite coffee</div>
            <div onClick={()=> onClickHandler('about')}>About</div>
            <div onClick={() => onClickHandler('mobileApp')}>Mobile app</div>
            <div onClick={() => onClickHandler('contactUs')}>Contact us</div>
            <div className={S.menuCup}>
              <N.NavWrapper>
                <NavLink to={PATH.MENU}>
                  <div onClick={onClickRetract} className={S.coffeeMenu}>
                    Menu
                    <SvgCup/>
                  </div>
                </NavLink>
              </N.NavWrapper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

