import React, {useEffect, useState} from 'react';
import {ICoffee, sliderData} from './sliderData'
import S from './SliderList.module.css'
import Prev from "./img/svg/Prev";
import Next from "./img/svg/Next";

export const SliderList = () => {

  const [mainCoffee, setMainCoffee] = useState(sliderData)
  const [index, setIndex] = useState(0)
  const [startX, setStartX] = useState(0);
  const [endX, setEndX] = useState(0);

  const handleTouchStart = (e: React.TouchEvent<HTMLDivElement>) => {
    setStartX(e.touches[0].clientX);
  };

  const handleTouchMove = (e: React.TouchEvent<HTMLDivElement>) => {
    setEndX(e.touches[0].clientX);
  };

  const handleTouchEnd = () => {
    if (startX - endX > 75) {
      setIndex(prevState => prevState + 1);
    } else if (startX - endX < -75) {
      setIndex(prevState => prevState - 1);
    }
  };

  useEffect(() => {
    const lastIndex = mainCoffee.length - 1
    if (index < 0) {
      setIndex(lastIndex)
    }
    if (index > lastIndex) {
      setIndex(0)
    }
  }, [index, mainCoffee]);

  useEffect(() => {
    let slider = setInterval(() => setIndex(prevState => prevState + 1), 10000)
    return () => {
      clearInterval(slider)
    }
  }, [index]);


  return (
    <div className={S.container} onTouchStart={handleTouchStart} onTouchMove={handleTouchMove}
         onTouchEnd={handleTouchEnd}>
      <div className={S.containerSliderBtn}>
        <Prev setIndex={() => setIndex(prevState => prevState - 1)}/>
        <div className={S.sliderContainer}>
          <div className={S.cup}>
            {sliderData.map((coffee: ICoffee, coffeeIndex) => {
                const {id, src, name, description, prise} = coffee;

                let position = `${S.nextSlide}`
                if (coffeeIndex === index) {
                  position = `${S.activeSlide}`
                }

                if (coffeeIndex === index - 1 || (index === 0 && coffeeIndex === mainCoffee.length - 1)) {
                  position = `${S.lastSlide}`
                }
                return (
                  <article className={position} key={id}>
                    <div className={S.slider}>
                      <img src={src} alt={name}/>
                      <div className={S.textContainer}>
                        <div className={S.nameText}>{name}</div>
                        <div className={S.descriptionText}>{description}</div>
                        <div className={S.priseText}>{prise}</div>
                      </div>
                    </div>
                  </article>

                )
              }
            )}
          </div>

        </div>
        <Next setIndex={() => setIndex(prevState => prevState + 1)}/>
      </div>
      <div id='about'></div>
      <div className={S.dots}>
        {sliderData.map((_, idx) => (
          <button
            key={idx}
            className={`${S.dot} ${index === idx && S.active}`}
            onClick={() => setIndex(idx)}
          />
        ))}
      </div>

    </div>

  )
}
