import * as React from 'react';
import S from './Header.module.css';
import {N} from './_stules'
import logo from './src/logo.png'
import cup from './src/coffee-cup.png'
import {NavLink} from "react-router-dom";
import {PATH} from "../../App";
import {Burger} from "./HeaderComponent/Burger/Burger";
import {useState} from "react";
import {Shop} from "./HeaderComponent/Shop/Shop";
import {Svg} from "./HeaderComponent/img/svg";
import {DisplayItem} from "../Body/Menu/MainDIsplay/MainDisplay";

type AddedCart = {
  addedCart: DisplayItem[];
  // onClickPlus: (id: string) => void;
  setAddedCart: (addedCart: DisplayItem[]) => void;
}

export const Header = (props: AddedCart) => {

  const [cartShop, setCartShop] = useState<boolean>(false)
  const [retractableMenu, setRetractableMenu] = useState<boolean>(false)

  const onClickRetract = () => {
    setRetractableMenu(!retractableMenu)
  }

  const onClickCart = () => {
    setCartShop(!cartShop)
  }

  const scrollToSelection = (selectionId: string) => {
    const element = document.getElementById(selectionId);
    if (element) {
      element.scrollIntoView({behavior: 'smooth'});
    }
  }

  const totalQuantity = props.addedCart.reduce((total, el) => total + el.quantity, 0);

  return (
    <div className={S.container}>
      <div className={S.header}>
        <NavLink to={PATH.HOME}>
          <img className={S.logo} src={logo} alt="logo"/>
        </NavLink>
        <div className={S.navBar}>
          <span className={S.navBarButton} onClick={() => scrollToSelection('favoriteCoffee')}>Favorite coffee</span>
          <span className={S.navBarButton} onClick={() => scrollToSelection('about')}>About</span>
          <span className={S.navBarButton} onClick={() => scrollToSelection('mobileApp')}>Mobile app</span>
          <span className={S.navBarButton} onClick={() => scrollToSelection('contactUs')}>Contact us</span>
        </div>
        <div className={S.cartShop} onClick={onClickCart}>
          <Svg/>
          <div className={S.quantity}>{totalQuantity}</div>
        </div>
        <div className={S.buttonContainerTablet} onClick={onClickRetract}>
          <div className={S.buttonAll}>
            <div className={`${S.buttonOne} ${retractableMenu ? S.active : ''}`}></div>
            <div className={`${S.buttonTwo} ${retractableMenu ? S.active : ''}`}></div>
          </div>
        </div>
        <N.NavWrapper>
          <NavLink to={PATH.MENU}>
            <div className={S.coffeeMenu}>
              Menu
              <img src={cup} alt="cup"/>
            </div>
          </NavLink>
        </N.NavWrapper>
      </div>
      <Shop state={cartShop} onClick={onClickCart} addedCart={props.addedCart} setAddedCart={props.setAddedCart}/>
      <Burger state={retractableMenu} onClickRetract={onClickRetract} scrollToSelection={scrollToSelection}/>
      <div className={S.grad}></div>
    </div>
  );
};