import React from 'react';
import S from './About.module.css'
import about1 from './src/about-1.jpg'
import about2 from './src/about-2.jpg'
import about3 from './src/about-3.jpg'
import about4 from './src/about-4.jpg'

export const About = () => {
  return (
    <div className={S.container}>
      <div className={S.textContainer}>
        Resource is <i>the perfect and cozy place</i> where you can enjoy a variety of hot beverages,
        relax, catch up with
        friends, or get some work done.
      </div>
      <div className={S.imagesContainer}>
        <div className={S.imagesCol1}>
          <div><img src={about1} alt="about1"/></div>
          <div><img src={about2} alt="about2"/></div>
        </div>
        <div className={S.imagesCol2}>
          <div><img src={about3} alt="about3"/></div>
          <div><img src={about4} alt="about4"/></div>
        </div>
      </div>
      <div id='mobileApp'></div>
    </div >
  );
};