import React, {Dispatch, SetStateAction} from 'react';
import {coffeeData, dessertData, ItemType, teaData} from "../data";
import S from './Button.module.css'
import DessertSvg from "./svg/DessertSvg";
import TeaSvg from "./svg/TeaSvg";
import CoffeeSvg from "./svg/CoffeeSvg";

type ButtonType = {
  data: ItemType[]
  setState: Dispatch<SetStateAction<ItemType[]>>
}

export const Button = (props: ButtonType) => {
  return (
    <div className={S.container}>
      <div className={S.buttons}>
        <div className={`${S.coffee} ${props.data === coffeeData ? S.active : ''} `}
             onClick={() => props.setState(coffeeData)}>
          <div className={S.svg}>
            <CoffeeSvg/>
          </div>
          <div className={S.text}>Coffee</div>
        </div>
        <div className={`${S.tea} ${props.data === teaData ? S.active : ''}`}
             onClick={() => props.setState(teaData)}>
          <div className={S.svg}>
            <TeaSvg/>
          </div>
          <div className={S.text} onClick={() => props.setState(teaData)}>Tea</div>
        </div>
        <div className={`${S.dessert} ${props.data === dessertData ? S.active : ''}`}
             onClick={() => props.setState(dessertData)}>
          <div className={S.svg}>
            <DessertSvg/>
          </div>
          <div className={S.text}>Dessert</div>
        </div>
      </div>
    </div>
  );
};
