import React, {useState} from 'react';
import {coffeeData, ItemType} from "./data";
import {DataDisplay, DisplayItem} from "./MainDIsplay/MainDisplay";
import {Button} from "./Botton/Button";

export type AddedItem = {
  addedCart: DisplayItem[]
  setAddedCart: (addedCart: (prevCart: DisplayItem[]) => ({
    id: string;
    title: string;
    price: number;
    size: string;
    src: string;
    quantity: number
  }[])) => void;
}

export const Menu = (props: AddedItem) => {
  const [currentData, setCurrentData] = useState<ItemType[]>(coffeeData);

  return (
    <div>
      <Button data={currentData} setState={setCurrentData}/>
      <DataDisplay data={currentData} addedCart={props.addedCart} setAddedCart={props.setAddedCart}/>
    </div>
  );
};

